import React from "react";
import "./css/dashboardElement.css";
import images from "../Assets";

export enum DElementShape {
  square,
  rectangle,
  cover
}
interface IDashboardElement {
  shape: DElementShape;
  image: string;
}

const DashboardElement: React.FC<IDashboardElement> = props => {
  const { shape, image } = props;
  let cssClassName: string;

  switch (shape) {
    case DElementShape.cover:
      cssClassName = "cDE_Cover";
      break;
    case DElementShape.rectangle:
      cssClassName = "cDE_Rectangle";
      break;
    case DElementShape.square:
    default:
      cssClassName = "cDE_Square";
      break;
  }
  return (
    <div className={cssClassName}>
      <img src={image} />
    </div>
  );
};

export default DashboardElement;
