import * as React from "react";
import DashboardElement, { DElementShape } from "./DashboardElement";
import "./css/dashboard.css";
import images from "../Assets";

interface IDashboardProps {}

const Dashboard: React.FunctionComponent<IDashboardProps> = props => {
  return (
    <div className="cDashboard_MainContainer">
      <DashboardElement
        shape={DElementShape.cover}
        image={images.productLines.makeup}
      ></DashboardElement>
      <DashboardElement
        shape={DElementShape.square}
        image={images.productLines.jewelry}
      ></DashboardElement>
      <DashboardElement
        shape={DElementShape.square}
        image={images.productLines.clothes}
      ></DashboardElement>
      <DashboardElement
        shape={DElementShape.rectangle}
        image={images.productLines.child}
      ></DashboardElement>
    </div>
  );
};

export default Dashboard;
